<template>
    <section>
        <tg-list v-show="IscrewView" :IsShowAction="true" ModuleName="FSM" SubModuleCode="FSM_CREW" :LegendArray="[]"
            :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick" :widgets="[]"
            IdentityColumn="crew_id" :HeaderText="$t('Crew')" :listType="['List']" :ListData="CrewList"
            :HeaderData="Headers" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :SearchFields="leftSearchFields" :ListDataCallBackFunction="FetchRecords" :showCheckBox="true"
            :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS_NAME'">
                    <a class="active">
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Active'">
                            {{ data.row[data.column.COLUMN_NAME] }}
                        </span>
                        <span v-bind:class="getStatusClassList(data.row.STATUS_NAME, 'STATUS_NAME')"
                            v-if="data.row.STATUS_NAME == 'Inactive'">
                            {{ $t('Inactive') }}
                        </span>
                    </a>
                </template>
            </template>
        </tg-list>
        <AddCrewPopup v-if="AddCrewPopup" :CrewId="ticketSourceId"></AddCrewPopup>
    </section>
</template>
<script>
import Schema from "@/views/crew/crewSchema";
import DataServices from "@/services/DataService";
import AddCrewPopup from '../../components/Modal/AddCrewPopup.vue'
export default {
    components: { AddCrewPopup },
    data() {
        return {
            IscrewView: true,
            AddCrewPopup: false,
            showSetupTickectSource: false,
            showExpanded: '',
            StatusMappingList: [],
            noRecords: false,
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            SearchCondition: '',
            CrewList: [],
            GroupStart: 1,
            GroupEnd: 3,
            ticketSourceId: 0,
            Headers: [{
                "COLUMN_NAME": "crew_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Crew Name",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "crew_size",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Crew Size",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "STATUS_NAME",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 3
            }
            ],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            user_type: null,
            user_id: null,
            companyId: null,
            search: ''
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        vm.user_type = this.GetUserInfo.UserType
        vm.companyId = this.GetUserInfo.CompanyId;
        await vm.FetchRecords();
    },
    methods: {
        paramsToObject(entries) {
            const result = {}
            for (const [key, value] of entries) { // each 'entry' is a [key, value] tupple
                result[key] = value;
            }
            return result;
        },
        leftCommonSearch: function (value) {
            const searchParams = new URLSearchParams(value.searchConditionJson);
            const entries = searchParams.entries();
            const params = this.paramsToObject(entries);
            if (params.hasOwnProperty('search')) {
                this.search = params['search'];
            }
            else {
                this.search = '';
            }
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchCondition;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchRecords();
        },
        actionButtonClick(callbackfunction, item, index, event) {
            switch (callbackfunction) {
                case 'Edit':
                    this.openPopupForSetupCrewList(item.crew_id)
                    break;
                case 'ClickOnView':
                    this.CrewDeatail(item.crew_id);
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'ClickOnView':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FetchRecords: async function () {
            var vueObj = this;
            if (!vueObj.isBlank(vueObj.SearchCondition)) {
                vueObj.PageNumber = 1;
            }
            var url = `sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}`;
            if (!vueObj.isBlank(this.search)) {
                url += `&search=${this.search}`;
            }
            await DataServices.GetFsmCrewList(url).then(function (response) {
                vueObj.Headers.forEach((column) => {
                    column.settings = null;
                    if (column.COLUMN_NAME == 'STATUS_NAME') {
                        column.DISPLAY_NAME = 'Status'
                        column.settings = {
                            isInSlot: true,
                            isForTag: true
                        }
                    }
                    if (column.COLUMN_NAME == 'View') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                    if (column.COLUMN_NAME == 'Edit') {
                        column.settings = {
                            isInSlot: true
                        }
                    }
                });
                vueObj.CrewList = [];
                vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                vueObj.TotalRecords = 0;
                if (response.data != null && response.data != "") {
                    if (response.data.status == "Success") {
                        vueObj.CrewList = response.data.result.data;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        if (vueObj.CrewList.length > 0) {
                            vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                            vueObj.listheaderbuttons[1].isdisabled=true;
                            vueObj.noRecords = false;
                        }
                        else {
                            vueObj.noRecords = true;
                        }
                    }
                    else {
                        vueObj.noRecords = true;
                    }
                    setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
                }
            });
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "openaddWorkType":
                    this.openPopupForSetupCrewList(null);
                    break;
            }
        },
        sortdata: function (sortBy) {

            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        openPopupForSetupCrewList: function (id) {
            if (id == undefined)
                id = 0;
            this.ticketSourceId = id;
            this.AddCrewPopup = !this.AddCrewPopup;
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Crew")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "FSM",
                        refCode: "TALYGEN_FSM_CREW"

                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataServices.deleteActivityData(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vm.FetchRecords();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Crew')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Crew')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
            else {
                alert(vm.$t("Selectanyrecordtodelete"));
            }
        },
        getStatusClassList: function (status) {
            var returnClass = '';
            switch (status) {
                case 'Active':
                    returnClass = 'px-2 text-center text-white d-block bg-success';
                    break;
                case 'Inactive':
                    returnClass = 'px-2 text-center text-white d-block bg-danger';
                    break;
                default:
                    returnClass = '';
            }
            return returnClass;
        },
        CrewDeatail(ths) {
            this.$router.push({ name: "CrewDetail", params: { id: ths } });
        }
    }
}
</script>
