<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('ManageServiceCrew') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditFSMCrew')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit"></dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        CrewId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('CrewName'),
                            name: "CrewName",
                            value: "",
                            placeholder: this.$t('EnterCrewName'),
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "NumericField",
                            label: this.$t('CrewSize'),
                            name: "CrewSize",
                            value: "",
                            placeholder: this.$t('EnterCrewSize'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: [{ value: "1001", name: "Active" }, { value: "1002", name: "InActive" }]
                            },
                            placeholder: this.$t('SelectStatus'),
                            validationRules: { "required": true, max: 100 },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: "",
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        this.FetchData();
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            var url = `Id=${vueObj.CrewId}`;
            DataServices.FetchDataByCrewId(url).then(
                function (response) {
                    if (response.data != null && response.data != "") {
                        if (response.data.status == "Success") {
                            (vueObj.FormSchema)[0].Data[0].value = response.data.result.data[0].crew_name;
                            (vueObj.FormSchema)[0].Data[1].value = response.data.result.data[0].crew_size;
                            (vueObj.FormSchema)[1].Data[0].value = response.data.result.data[0].status_id;
                            (vueObj.FormSchema)[2].Data[0].value = response.data.result.data[0].description;
                        }
                    }
                }
            );
        },
        Close: function () {
            this.$parent.AddCrewPopup = false;
        },
        onSubmit(formfields, val) {
            var vm = this;
            var jsonobj = {
                Id: vm.CrewId == null ? 0 : vm.CrewId,
                CrewName: formfields.CrewName,
                CrewSize: formfields.CrewSize,
                FSMTerritoryListId: null,
                Desc: formfields.Description,
                Status: formfields.Status
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveTicketingCrew(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Crew")]), "failure", true, vm.$t("Alert"));
                    }
                    else {
                        if (vm.CrewId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Crew")]), "success", true, vm.$t("Alert"));
                            vm.Close();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Crew")]), "success", true, vm.$t("Alert"));
                        }
                        vm.Close();
                        vm.$parent.FetchRecords();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Crew")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>